.Home{
    position: relative;
    height: 100%;
    width: 50%;
}


.bottom-left{
    width: 25%;
    height: auto;
    position: fixed;
    bottom: 0%;
    margin-left: -26.5%;
}

.top-right{
    width: 25%;
    height: auto;
    position: fixed;
    top: 0%;
    margin-left: 1.5%;
}

.home-text{
    align-items: center;
    padding-top: 20%;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    gap: 0px;
}

.title{
    font-family: Italiana;
    font-size: 80pt;
    width: 50%;
    font-weight: lighter;
    padding: none;
}

.name{
    font-family: Inter;
    font-weight: 300;
    margin-top: -5%;
}

.home-selections{
    padding-top: 3%;
    display: flex;
    flex-direction: column;
    line-height: 0%;
}

.home-menu-selection{
    display: flex;
    width: 100%;
    align-items: center;
    padding-left: 10%;
    justify-content: center;
    flex-direction: row;
    font-size: 18pt;
    font-family: Italiana;
    font-weight: 100;
    text-decoration: none;
    color: black;
    transition: 1s;
}

.home-menu-selection:hover {text-decoration: underline;}

.home-add-selection{
    display: flex;
    width: 100%;
    align-items: center;
    padding-left: 22%;
    justify-content: center;
    flex-direction: row;
    font-size: 18pt;
    font-family: Italiana;
    font-weight: 100;
    text-decoration: none;
    color: black;
    transition: 1s;
}

.home-add-selection:hover {text-decoration: underline;}

.home-about-selection{
    display: flex;
    width: 100%;
    align-items: center;
    padding-left: 32%;
    justify-content: center;
    flex-direction: row;
    font-size: 18pt;
    font-family: Italiana;
    font-weight: 100;
    text-decoration: none;
    color: black;
    transition: 1s;
}

.home-about-selection:hover {text-decoration: underline;}

.arrow-right{
    position: fixed;
    right: 5%;
    top: 40%;
    transition: 0.5s;
}

.arrow-right:hover {cursor: pointer; filter: invert(1); right: 4%}

@media (max-width: 1030px){
    .title{
        margin-left: -15%;
    }
    .bottom-left{
        margin-left: -27%;
    }
    .top-right{
        margin-left: 2%;
    }
}

@media (max-width: 780px){
    .Home{
        width: 100%;
        height: 100%;
    }

    .bottom-left{
        display: none;
    }

    .top-right{
        display: none;
    }

    .home-text{
        padding-top: 30%;
        margin-left: 10%;
    }

    .home-menu-selection{
        padding-left: 0%;
    }

    .home-about-selection{
        padding-left: 0%;
    }

    .home-add-selection{
        padding-left: 0%;
    }

    .name{
        display: none;
    }

    .arrow-right{
        height: 10%;
        z-index: 3;
    }

}